.container {
  width: 80%;
  margin: 3.75rem auto;
  padding: 0 1.25rem;
}

.accordion {
  margin: 0;
  padding: 0;
  background: #464749;
  color: #00432f;
  border: 1px solid #d7d9d8;
  cursor: pointer;
}

.accordion__item {
  position: relative;
  list-style-type: none;
  padding: 2% 4%;
}

.accordion__item:not(:last-of-type) {
  border-bottom: 1px solid #d7d9d8;
}

input[type="checkbox"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}

.accordion__title {
  padding-right: 1.5rem;
  font-size: 1.25rem;
}

.accordion__arrow {
  position: absolute;
  right: 0;
  margin-top: 26px;
  margin-right: 6%;
}

.accordion__arrow::before,
.accordion__arrow::after {
  content: "";
  position: absolute;
  background-color: #00432f;
  width: 3px;
  height: 9px;
  transition: transform 0.25s ease-in-out;
}

.accordion__arrow::before {
  transform: translateX(-2px) rotate(45deg);
}

.accordion__arrow::after {
  transform: translateX(2px) rotate(-45deg);
}

input[type="checkbox"]:checked ~ .accordion__arrow::before {
  transform: translateX(2px) rotate(45deg);
}

input[type="checkbox"]:checked ~ .accordion__arrow::after {
  transform: translateX(-2px) rotate(-45deg);
}

.vs-container {
  position: relative;
  /* max-height: 200px; */
  width: 100%;
  margin: 0;
  opacity: 1;
  overflow: hidden;
  transition: all 0.35s ease-in-out;
  line-height: 1.6;
  z-index: 2;
  border: 1px solid black;
}

input[type="checkbox"]:checked ~ .vs-container{
  max-height: 0;
  opacity: 0;
}

